import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', redirect: '/home'},
    {
      path: '/home',
      name: 'Home',
      component: () => import('@/components/home')
    },
    {
      path: '/invited',
      name: 'Invited',
      component: () => import('@/components/Invited')
    },
    {
      path: '/finsh',
      name: 'Finshed',
      component: () => import('@/components/finsh')
    }
  ]
})



